.wp-block-carkeek-custom-link-list a {
  text-decoration: none; }
  .wp-block-carkeek-custom-link-list a:hover {
    text-decoration: underline; }

.wp-block-carkeek-custom-link-list .cll-label {
  font-weight: 900; }

.wp-block-carkeek-custom-link-list .cll-notes {
  font-style: italic; }

.wp-block-carkeek-custom-link-list h3, .wp-block-carkeek-custom-link-list h4, .wp-block-carkeek-custom-link-list h5, .wp-block-carkeek-custom-link-list h6 {
  font-size: 18px;
  font-weight: 900;
  color: #333;
  margin-bottom: 10px; }

.carkeek-blocks-accordion.mini .ckb-accordion-label button {
  background-color: transparent;
  color: #007DAA;
  padding-left: 0;
  padding-right: 0; }
  .carkeek-blocks-accordion.mini .ckb-accordion-label button:focus {
    outline: none;
    color: #00233E;
    border-color: transparent; }
  .carkeek-blocks-accordion.mini .ckb-accordion-label button:before {
    content: '\002B';
    padding-right: 10px; }
  .carkeek-blocks-accordion.mini .ckb-accordion-label button[aria-expanded="true"]:before {
    content: '\2212'; }

.carkeek-blocks-accordion.mini .ckb-accordion-panel {
  display: none;
  padding-left: 21px; }
