.wp-block-carkeek-blocks-link-tiles {
  display: flex;
  flex-direction: column; }

.wp-block-carkeek-blocks-link-tile {
  width: 100%;
  position: relative;
  color: #fff;
  max-height: 350px; }
  .wp-block-carkeek-blocks-link-tile span {
    display: block;
    padding-right: 10%;
    padding-left: 10%;
    font-size: 1.5rem;
    font-weight: 700;
    text-align: center;
    width: 100%; }
  .wp-block-carkeek-blocks-link-tile .link-tile__hover_title {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    transform: translate(-50%, -50%);
    opacity: 0; }
    .wp-block-carkeek-blocks-link-tile .link-tile__hover_title:after {
      content: "\203A";
      margin-left: 5px; }
  .wp-block-carkeek-blocks-link-tile a {
    color: var(--color-theme-white); }
    .wp-block-carkeek-blocks-link-tile a:visited {
      color: var(--color-theme-white); }
    .wp-block-carkeek-blocks-link-tile a:hover > .wp-block-carkeek-blocks-link-tile__img, .wp-block-carkeek-blocks-link-tile a:focus > .wp-block-carkeek-blocks-link-tile__img {
      opacity: 0; }
    .wp-block-carkeek-blocks-link-tile a:hover .link-tile__hover_title, .wp-block-carkeek-blocks-link-tile a:focus .link-tile__hover_title {
      color: var(--color-theme-white);
      opacity: 1; }

.wp-block-carkeek-blocks-link-tile__img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.3s ease;
  z-index: 20; }

.wp-block-carkeek-blocks-link-tile__link {
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center; }
  .wp-block-carkeek-blocks-link-tile__link:after {
    content: "";
    display: block;
    padding-bottom: 100%;
    width: 100%; }

@media screen and (min-width: 768px) {
  .wp-block-carkeek-blocks-link-tiles {
    flex-direction: row;
    flex-wrap: nowrap; } }
